.App {
  font-family: sans-serif;
  /* text-align: center; */
}

div > pre {
  counter-reset: line;
}

div > pre > code {
  counter-increment: line;
  text-align: left;
  background-color: black;
  color: yellow;
  font-size: 20px;
}

div > pre > code:before {
  content: counter(line);
  user-select: none;
  -webkit-user-select: none;
}

div > code {
  text-align: left;
  background-color: black;
  color: yellow;
  font-size: 20px;
}

div > code > span {
  display: block;
  background-color: black;
}

div > button {
  max-width: 70px;
}

.sameLine {
  display: flex;
  justify-content: flex-start;
}

.sameLine button {
  max-width: 120px;
  margin: 0 5px;
}

.sameLine input {
  max-width: 70px;
  margin: 0 10px;
}

.sameCol {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}

.goodjob {
  background-color: chartreuse;
}
